/* -------------------------home page -----------------------*/
/* ----------------------------------------------------------*/

.title {
  font-size: 4em;
  padding: 8% 0px 0px 50px;
  display: block;
}

.scroller {
  z-index: 900;
}

#name {
  font-size: 4em;
  padding-top: 250px;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid black;
  margin-bottom: -1px;
}

#aspiring {
  font-size: 20px;
  font-family: "Oxygen";
  margin-top: 7px;
}

.logos {
  justify-content: space-around;
  width: 45px;
  height: 45px;
  padding: 3% 0px 225px 0px;
  margin: 20px;
  cursor: pointer;
}

.logos:hover {
  opacity: 0.5;
}

.short-nav ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-right: 20px;
}

.short-nav ul li {
  color: rgb(56, 55, 55);
  font-size: 20px;
  margin-right: 10px;
}

.short-nav ul li:hover {
  font-size: 22px;
  font-weight: bolder;
  cursor: pointer;
}

@media (max-width: 700px) {
  .logos {
    padding-top: 0.005%;
  }
}

.about-title {
  font-size: 4em;
  display: block;
  padding: 8% 0px 0px 50px;
}

.intro-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-left: 8%;
}

.about-image {
  width: 245px;
  height: 255px;
  border-radius: 50%;
  margin-right: 20px;
  border: 7px solid white;
}

#let-me {
  font-size: 40px;
  padding-top: 1%;
}

#introduction {
  font-family: "Oxygen";
  font-size: 20px;
  padding-right: 50px;
  max-width: 800px;
}

.basic-info {
  font-size: 25px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding-bottom: 50px;
}

.inner-basic-info {
  width: 600px;
  padding: 30px 0px 0px 150px;
}

#resume {
  background-color: white;
  border: 1px solid black;
  color: black;
  font-family: "Oswald";
  font-size: 20px;
  padding: 10px 40px 10px 40px;
  margin: 80px;
  border-radius: 20px;
  cursor: pointer;
}

@media (max-width: 900px) {
  #introduction {
    text-align: center;
    margin: 10px 15px 0px 15px;
    padding-right: 0px;
  }
  #let-me {
    text-align: center;
  }
  .intro-container {
    flex-direction: column;
    align-items: center;
    padding-left: 0px;
  }
  .basic-info {
    flex-direction: column;
    align-items: center;
  }
  .inner-basic-info {
    text-align: center;
    padding-left: 0px;
  }

  #resume {
    margin: 40px;
  }
}

@media (max-width: 530px) {
  .about-title {
    text-align: center;
    padding: 8% 0px 20px 0px;
  }
}

.exper-title {
  font-size: 4em;
  display: block;
  padding: 8% 0px 0px 50px;
}

.education {
  font-size: 2em;
  display: block;
  padding: 50px;
}

.exper-text {
  font-size: 20px;
  font-family: "Oxygen";
  text-align: left;
  padding: 0px 0px 20px 100px;
}

.vert-timeline {
  -webkit-filter: drop-shadow(0 6px 20px #b9b9b9ab);
          filter: drop-shadow(0 6px 20px #b9b9b9ab);
}

@media (max-width: 530px) {
  .exper-title {
    text-align: center;
    padding: 8% 0px 0px 0px;
  }
  .exper-text {
    text-align: center;
    padding: 0px 0px 20px 0px;
  }
  .education {
    text-align: center;
  }
}

/* ------------------------projects page----------------------- */
/* -------------------------------------------------------------*/

.proj-title {
  font-size: 4em;
  display: block;
  padding: 8% 0px 0px 50px;
}

.project-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  text-align: left;
}

.project-link {
  display: block;
  flex-flow: column;
  width: 500px;
  box-shadow: 0 6px 20px white;
  -webkit-filter: drop-shadow(0 6px 20px white);
  filter: drop-shadow(0 6px 20px white);
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  padding-bottom: 20px;
  margin: 20px 0px 20px 30px;
  background-color: #f4f0f5;
}

.fade-img {
  animation-name: fade-img;
  animation-duration: 2s;
}

.project-tags {
  position: relative;
  width: 100%;
  padding-top: 50%;
  overflow: hidden;
}

.project-tags::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 15px;
  font-weight: 700;
  color: black;
  background-color: #f5dddd;
  box-sizing: border-box;
}

.project-img {
  position: absolute;
  text-align: center;
  border-radius: 30px;
  cursor: pointer;
  border: 2px solid black;
  top: 0;
  width: 80%;
  height: 100%;
  margin-left: 3%;
  transition: all 0.2s linear;
}

.project-img:hover {
  transform: scale(0.95);
  opacity: 0.5;
}

.project-info {
  padding-left: 30px;
}

.project-text {
  color: black;
  font-size: 25px;
  width: 450px;
  align-items: center;
}

.instru {
  color: black;
  text-align: center;
  margin: 0px 15px 0px 15px;
}

@media only screen and (max-width: 1150px) {
  .project-container {
    margin-left: 20%;
  }
  .project-link {
    width: 70%;
  }
}

@media only screen and (max-width: 909px) {
  .project-img {
    margin-left: 0px;
  }
}

@media (max-width: 530px) {
  .proj-title {
    text-align: center;
    padding: 8% 0px 20px 0px;
  }
}

#end-padding {
  padding: 70px;
}

/* ------------------------contact page----------------------- */
/* -------------------------------------------------------------*/

.contact-title {
  font-size: 4em;
  display: block;
  padding: 8% 0px 0px 50px;
}

.contact-blurb {
  font-size: 20px;
  font-family: "Oxygen";
  text-align: left;
  padding: 0px 0px 20px 100px;
}

.contact-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  text-align: center;
}

.contact-link {
  -webkit-filter: drop-shadow(0 6px 20px #f5dddd);
          filter: drop-shadow(0 6px 20px #f5dddd);
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
  padding-bottom: 20px;
  color: black;
  font-size: 30px;
}

.contact-img {
  display: block;
  width: 80px;
  height: 80px;
  transition: all 0.2s linear;
  margin: 0px auto 0px auto;
}

.contact-info {
  flex: 1 1;
  align-items: baseline;
  margin: 0 1rem;
  border-radius: 10px;
  padding: 10px 0px 20px 30px;
  -webkit-filter: drop-shadow(0 6px 20px #f5dddd);
          filter: drop-shadow(0 6px 20px #f5dddd);
  border-radius: 10px;
  text-decoration: none;
  color: black;
  font-size: 30px;
}

.contact-caption {
  font-family: "Oswald";
  font-weight: bold;
  margin: 30px 0px 35px 0px;
}

.contact-text {
  color: black;
  font-size: 25px;
  align-items: center;
  font-weight: lighter;
  text-decoration: underline;
  cursor: pointer;
}

.contact-text:hover {
  opacity: 0.5;
}

@media (max-width: 900px) {
  .contact-container {
    display: flex;
    flex-direction: column;
  }
  .contact-info {
    padding: 0px 0px 20px 0px;
  }
  .contact-caption {
    margin: 5px 0px 5px 0px;
  }
  .contact-text {
    margin-top: 15px;
  }
  .contact-img {
    margin-top: 14px;
  }
}

@media (max-width: 530px) {
  .contact-title {
    text-align: center;
    padding: 8% 0px 5px 0px;
  }
  .contact-blurb {
    text-align: center;
    padding: 0px 0px 15px 0px;
  }
}

/* ------------------------navigation bar----------------------- */
/* -------------------------------------------------------------*/

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: white;
  z-index: 1000;
}

.navbar ul {
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.navbar ul li {
  color: black;
  font-size: 2em;
}

.navbar ul li > .active {
  border-bottom: 1px solid black;
}

.navbar ul li:hover {
  font-size: 35px;
  font-weight: bolder;
  cursor: pointer;
}

.home-section {
  background-image: url(/static/media/pastelback.ae5fc228.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.about-section {
  background-color: #f2edf4;
  align-content: stretch;
}

.experiences-section {
  background-color: #cbb8d0;
  z-index: 800;
}

.projects-section {
  background-color: #efeaf1;
}

.contact-section {
  background-color: #a68cb1;
}

